const Header = ({ className, children, ...props }) => (<div className={`${classNames(className)} bg-blue-primary header grid grid-cols-[2fr_8fr] p-6`} {...props}>
  {children}
</div>);

const Main = ({ className, children, style, ...props }) => (
  <div
    className={`${classNames(className)} Main bg-gray-50 grid gap-4 p-4`}
    style={{
      gridTemplate: `
        'a b b b b c c c'
        'a d d e e e f f'`,
      ...style
    }}
    {...props}
  >
    {children}
  </div>
);

const PlaceholderPanel = ({className, children, ...props}) => (
  <div className={`${classNames(className)} PlaceholderPanel bg-white p-4 border-2 border-gray-200`} {...props}>{children}</div>
);

const IconText = ({className, children, ...props}) => (
  <div className={`IconText ${classNames(className)}}`} {...props}>{children}</div>
);

const AgendaToday = ({className, children, meetingData, ...props}) => (
  <table className={`${classNames('AgentToday', className)}}`}>
    {meetingData.meetings.map((meetingItem) => {
      return (
        <tr key={meetingItem.meetingID}>
          <td>{meetingItem.meetingName}</td>
          <td>{meetingItem.meetingTime}</td>
          <td>
            <button onClick={(e) => {
              'use client';
              meetingData.onReschedule(e, meetingItem);
            }}>
              Reschedule
            </button>
          </td>
          <td>
            <button onClick={(e) => {
              'use client';
              meetingData.onChangeAttendance(e, meetingItem);
            }}>
              Change attendance
            </button>
          </td>
        </tr>
      );
    })}
  </table>
);

